.trashcan-container {
  width: 15%;
  height: 10%;

  position: relative;

  display: flex;
  align-self: center;
  justify-content: center;
}

.post-it-controls {
  width: 100%;
  height: 15%;

  position: relative;

  display: flex;
  align-self: center;
  justify-content: center;
}

.post-it-controls * {
  pointer-events: none;
}

.trashcan-image {
  object-fit: contain;
  max-height: 100%;
}
